<template>
  <Submenu :name="`${parentName}`">
    <template slot="title">
      <Icon :type="parentItem.icon || ''" :size="rootIconSize" />
      <span>{{ parentTitle }}</span>
    </template>
    <template v-for="(item, index) in children">
      <template v-if="item.children && item.children.length === 1">
        <menu-item :name="getRouteNameOrHref(item.children[0])" :key="index">
          <Icon :type="item.children[0].icon || ''" :size="iconSize" />
          <span>{{ item.children[0].title }}</span>
        </menu-item>
      </template>
      <template v-else>
        <side-menu-item v-if="showChildren(item)" :key="index" :parent-item="item" />
        <menu-item v-else :name="getRouteNameOrHref(item)" :key="index">
          <Icon :type="item.icon || ''" :size="iconSize" />
          <span>{{ item.title }}</span>
        </menu-item>
      </template>
    </template>
  </Submenu>
</template>

<script>
import mixin from './mixin'

export default {
  name: 'SideMenuItem',

  mixins: [mixin],

  components: {},

  props: {
    parentItem: {
      type: Object,
      default: () => {}
    },

    theme: String,

    iconSize: Number,

    rootIconSize: Number
  },

  computed: {
    parentName() {
      return this.parentItem.name
    },

    parentTitle() {
      return this.parentItem.title
    },

    children() {
      return this.parentItem.children
    },

    textColor() {
      return this.theme === 'dark' ? '#fff' : '#495060'
    }
  }
}
</script>
