<template>
  <div class="bread-crumb">
    <Breadcrumb :style="{ fontSize: `${fontSize}px` }">
      <BreadcrumbItem
        v-for="(item, index) in breadCrumbList"
        :to="index ? '' : item.router"
        :key="index"
      >
        <Icon class="icon" v-if="showIcon" :type="item.icon || ''" />
        {{ item.name }}
      </BreadcrumbItem>
    </Breadcrumb>
  </div>
</template>

<script>
export default {
  name: 'BreadCrumb',

  props: {
    breadCrumbList: {
      type: Array,
      default: () => []
    },

    fontSize: {
      type: Number,
      default: 14
    },

    showIcon: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style lang="less">
.bread-crumb {
  display: inline-block;
  vertical-align: top;

  .icon {
    margin-right: 4px;
  }
}
</style>
