<template>
  <Dropdown
    ref="dropdown"
    @on-click="handleClick"
    :class="hideTitle ? '' : 'collased-menu-dropdown'"
    :transfer="hideTitle"
    :placement="placement"
  >
    <a
      class="drop-menu-a"
      type="text"
      @mouseover="handleMousemove($event, children)"
      :style="{ textAlign: !hideTitle ? 'left' : '' }"
    >
      <Icon :size="rootIconSize" :color="textColor" :type="parentItem.icon" />
      <span class="menu-title" v-if="!hideTitle">{{ parentItem.title }}</span>
      <Icon style="float: right;" v-if="!hideTitle" type="ios-arrow-forward" :size="16" />
    </a>
    <DropdownMenu ref="dropdown" slot="list">
      <template v-for="(child, index) in children">
        <collapsed-menu
          v-if="showChildren(child)"
          :icon-size="iconSize"
          :parent-item="child"
          :key="index"
        ></collapsed-menu>
        <DropdownItem v-else :key="index" :name="child.name">
          <Icon :size="iconSize" :type="child.icon" />
          <span class="menu-title">{{ child.title }}</span>
        </DropdownItem>
      </template>
    </DropdownMenu>
  </Dropdown>
</template>

<script>
import mixin from './mixin'

export default {
  name: 'CollapsedMenu',

  mixins: [mixin],

  components: {},

  props: {
    parentItem: {
      type: Object,
      default: () => {}
    },

    theme: String,

    iconSize: Number,

    hideTitle: {
      type: Boolean,
      default: false
    },

    rootIconSize: Number
  },

  data() {
    return {
      placement: 'right-end'
    }
  },

  computed: {
    parentName() {
      return this.parentItem.name
    },

    children() {
      return this.parentItem.children
    },

    textColor() {
      return this.theme === 'dark' ? '#fff' : '#495060'
    }
  },

  mounted() {
    let dropdown = this.findNodeUpperByClasses(this.$refs.dropdown.$el, [
      'ivu-select-dropdown',
      'ivu-dropdown-transfer'
    ])
    if (dropdown) dropdown.style.overflow = 'visible'
  },

  methods: {
    handleClick(name) {
      this.$emit('on-click', name)
    },

    handleMousemove(event, children) {
      const { pageY } = event
      const height = children.length * 38
      const isOverflow = pageY + height < window.innerHeight
      this.placement = isOverflow ? 'right-start' : 'right-end'
    },

    findNodeUpperByClasses(ele, classes) {
      let parentNode = ele.parentNode
      if (parentNode) {
        let classList = parentNode.classList
        if (classList && classes.every(className => classList.contains(className))) {
          return parentNode
        } else {
          return this.findNodeUpperByClasses(parentNode, classes)
        }
      }
    }
  }
}
</script>
